* {
    box-sizing: border-box;
    user-select: none;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overscroll-behavior: none;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

header {
    display: flex;
    flex-direction: row;
    position: relative;
    padding: 1em;
    justify-content: center;
    align-items: center;
    background-color: var(--main-color);
}

.icon-container {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: fit-content;
    user-select: none;
}

.menu-button,
.reset {
    position: absolute;
    padding: 0.5rem;
    font-size: 1.5rem;
}

.menu-button {
    right: 1rem;
}

.reset {
    left: 1rem;
    color: red;
    font-weight: bold;
    border-radius: 20px;
}

.desktop {
    display: none;
}

.game-control-bar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 20%;
    font-size: 2em;
    justify-content: space-between;
    & > * {
        margin-bottom: 5px;
    }
}

@media only screen and (min-width: 768px) {
    .desktop {
        display: initial;
    }

    .reset > svg {
        margin-right: 0.5rem;
    }

    .menu-button > svg {
        margin-right: 0.5rem;
    }

    .reset:hover {
        background-color: red;
        color: var(--main-color);
        transition: all ease-in-out 0.1s;
        text-shadow: gray -1px -1px 2px;
        box-shadow: grey 1px 1px 2px 2px;
    }

    .game-control-bar {
        flex-direction: row;
    }
}

.moves {
    font-size: 2rem;
    text-align: center;
}

.board {
    border: solid black;
    display: grid;
    min-height: 50vh;
    min-width: 50vh;
    margin: 0 auto auto auto;
    @media only screen and (min-width: 768px) {
        margin: auto;
    }
}

.board-row {
    position: relative;
    display: flex;
    height: 100%;
}

.board-cell {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 0;
    left: 0;
    transition: all 1s linear;
    width: 100%;
    box-sizing: border-box;
    font-family: "Fredoka One", cursive;
    font-size: 2rem;
    background-color: var(--main-color);
}
